import { Injectable } from '@angular/core';
import {PROJECTS} from '../project/project-list/mock-projects';
import {Observable, of} from 'rxjs';
import {IProject} from '../project/IProject';
import {HttpClient} from '@angular/common/http';
import {ProjectPage} from './project-page';
import {ProjectCreation} from '../project/project-search/project-creation';

@Injectable({
  providedIn: 'root'
})
export class ProjectService {

  private URL = '/api/projects';

  constructor(private http: HttpClient) { }

  fetchAll(page: number, sortOrder: string): Observable<ProjectPage> {
    return this.http.get<ProjectPage>(this.URL + `?page=${page}&sort=datePosted,${sortOrder}`);
  }

  fetchById(id: number): Observable<IProject> {
    return this.http.get<IProject>(this.URL + `/${id}`);
  }

   post(project: ProjectCreation): Observable<IProject> {
    return this.http.post<IProject>(this.URL, project);
  }

  // TODO remove the hardcoded sort
  searchProjects(page: number, sortOrder: string, values: ProjectCreation): Observable<ProjectPage> {
    return this.http.get<ProjectPage>(
      this.URL
      + `?title=${values.title}`
      + `&difficulty=${values.difficulty}`
      + `&description=${values.description}`
      + `&page=${page}`
      + `&sort=datePosted,${sortOrder}`
    );
  }
}
